<template>
  <main class="content container">
    <div v-if="cartProduct.length === 0">
      Козина пуста. Добавьте товары для оформления заказа
      <router-link class="breadcrumbs__link" :to="{ name: 'main' }">
        Перейти на главную
      </router-link>
    </div>
    <div v-else>
      <div class="content__top">
        <ul class="breadcrumbs">
          <li class="breadcrumbs__item">
            <router-link class="breadcrumbs__link" :to="{ name: 'main' }">
              Каталог
            </router-link>
          </li>
          <li class="breadcrumbs__item">
            <a class="breadcrumbs__link">
              Корзина
            </a>
          </li>
        </ul>

        <h1 class="content__title">
          Корзина
        </h1>
        <span class="content__info"> {{ cartProduct.length }} товара </span>
      </div>

      <section class="cart">
        <form class="cart__form form" action="#" method="POST">
          <div class="cart__field">
            <ul class="cart__list">
              <CartItem v-for="item in cartProduct" :key="item.productId" :item="item" />
            </ul>
          </div>

          <div class="cart__block">
            <p class="cart__desc">
              Мы&nbsp;посчитаем стоимость доставки на&nbsp;следующем этапе
            </p>
            <p class="cart__price">
              Итого: <span>{{ cartPrice | numberFormat }} ₽</span>
            </p>
            <router-link
              tag="button"
              :to="{ name: 'order' }"
              class="cart__button button button--primery"
              type="submit"
            >
              Оформить заказ
            </router-link>
          </div>
        </form>
      </section>
    </div>
  </main>
</template>

<script>
import numberFormat from '@/helpers/numberFormat'
import { mapGetters } from 'vuex'
import CartItem from '@/components/CartItem.vue'

export default {
  name: 'CartPage',
  components: {
    CartItem,
  },
  filters: {
    numberFormat,
  },
  methods: {
    changeAmount(productId, amount) {
      this.$store.commit('updateCartProductAmount', {
        productId,
        amount,
      })
    },
  },
  computed: {
    ...mapGetters({ cartProduct: 'cartDetailProducts', cartPrice: 'cartTotalPrice' }),
  },
}
</script>
